<template>
  <div class="content">
    <ul class="one-kind">
      <li v-for="(item, index) in typeList" :class="index == page ? 'active' : ''" :key="item.id" @click="to(index)">
        {{item.name}}
      </li>
    </ul>
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(item, index) in typeList" :key="index" v-if="index == page">
          <router-link tag="div" :to="{name:'scLanmu',query:{cate_id:ite.id}}" div v-for="(ite, i) in item.children" :key="i" class="li_list">
            {{ite.name}}
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Swiper from 'swiper'
  import axios from  'axios'
  import { apiGet } from '../common/js/api';
  export default {
    data () {
      return {
        typeList: [],
        mySwiper: '',
        page: 0,

      }
    },
    created(){
      this.getGoodsType()
    },
    mounted () {
      let that = this;
      this.$nextTick(() => {
        this.mySwiper = new Swiper ('.swiper-container', {
          direction: 'vertical',
          // loop: true,
          controller: true,
          pagination: {
            el: '.swiper-pagination',
          },
          on: {
            slideChangeTransitionEnd: function(){
               console.log(this.activeIndex);//切换结束时，告诉我现在是第几个slide
               that.page = this.activeIndex
            }
          }
        })
      })
    },
    watch: {
      page (nPage) {
       console.log(nPage)
      }
    },
    methods: {
      //获取商品分类列表
      getGoodsType(){
        apiGet("/goods/goods-cate",{}).then(res=>{
          console.log(res)
          this.typeList = res.data.data.data;
        })
      },
      to (index) {
        console.log(index)
        this.mySwiper.slideTo(index)
        this.page = index
      }
    }
  }
</script>

<style lang='scss' scoped>
  .content{
    width: 100%;
    height: 100%;
    overflow: hidden;
    display: flex;
    .one-kind{
      width: 1.25rem;
      height: 100%;
      overflow: auto;
      background:linear-gradient(180deg,rgba(70,133,238,1),rgba(155,193,255,1));
      li{
        width: 100%;
        height: 0.68rem;
        text-align: center;
        line-height: 0.68rem;
        font-size: 0.17rem;
        color: white;
        &.active{
         background: white;
          color: #3C7FEE;
        }
      }
    }
    .swiper-container {
      flex: 1;
      height: 100%;
      width: 100%;

      .swiper-slide{
        height: 100%;
        padding: 0.18rem 0rem 0.18rem 0.13rem;
      }
      .li_list{
        float: left;
        padding: 0.15rem;
        border-radius: 0.04rem;
        font-size: 0.15rem;
        color: #333;
        text-align: center;
        line-height: 0.15rem;
        margin-right: 0.08rem;
        margin-bottom: 0.13rem;
        background: #E5E8EB;
      }
      // padding: 10px;
    }
  }
</style>



